.from-work #Map {
    opacity: 0.3;
}
#Map {
    text-align: right;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-content: center;
}

#Map>svg.background {
    max-width: 540px;
    max-height: 316px;
    margin-top: 50px;
}

#Map>svg.background>text {
    font-weight: bold;
    font-size: 11px;
    user-select: none;
}

#Map>svg polygon {
    opacity: 0;
}

#Map>h4 {
    margin-inline-start: 15px;
    font-weight: bold;
}

#Map>.info_box {
    width: 170px;
    position: absolute;
    right: 60%;
    font-size: 12px;
    overflow: hidden;
}

#Map>.info_box th {
    height: 21px;
}

#Map>.info_box tr>td:first-child {
    width: 30%;
}

#Map .CoordinatesForm {
    position: relative;
    bottom: 30px;
    right: 30px;
    width: fit-content;
    max-width: 150px;
}

#Map .CoordinatesForm button {
    font-size: 14px;
    font-weight: bold;
    padding: 2px 5px;
    /* margin: 0px 3px; */
}

#Map .CoordinatesForm input {
    text-align: right;
    height: 27px;
    width: 45px;
    border: 1px solid #71d000;
    border-radius: 5px;
    margin-left: 5px;
}

#Map svg.direction {
    width: 100px;
    height: 57px;
    position: absolute;
    right: 60%;
    bottom: 0px;
}

@media screen and (max-width: 960px) {
    #Map .CoordinatesForm {
        right: 10px;
        bottom: 15px;
    }

    #Map>.info_box {
        width: 130px;
        top: 4vh;
        /* right: unset; */
        /* left: 10px; */
        /* left: 0px !important; */
    }
}